/* Skills */

.l {
    position: relative;
    list-style: none;
    margin: 50px 0;
    font-weight: 500;
    text-transform: uppercase;
}

.l:before {
    content: "";
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    width: 100%;
    height: 15px;
    background-color: rgba(54, 126, 223, .5);
    border-radius: 1000px;
}

.l:after {
    content: "";
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    width: 0;
    height: 15px;
    background-color: rgba(54, 126, 223, 1);
    border-radius: 1000px;
    animation-duration: .7s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
}

.l.html:after {
    animation-name: html;
}

@keyframes html {
    to {
        width: 90%;
    }
}

.l.css:after {
    animation-name: css;
}

@keyframes css {
    to {
        width: 80%;
    }
}

.l.js:after {
    animation-name: js;
}

@keyframes js {
    to {
        width: 75%;
    }
}

.l.ng:after {
    animation-name: ng;
}

@keyframes ng {
    to {
        width: 60%;
    }
}

